<template>
	<div class="affirm_box">
		<header>
			<Headnav head_left="" head_title='确认订单' head_right="">
				<router-link  to="/city" slot="search" tag="span">
					<van-icon name="arrow-left" />
				</router-link>
				<router-link to="/user" slot="contact" tag="span">
					<van-icon name="contact" />
				</router-link>
			</Headnav>
		</header>
		<div class="affirm_cont">
			<router-link v-if="flag" to="/shopSite" tag="div" class="affirm_site">
				<p><van-icon class="location" name="location-o"/>请添加一个收获地址</p>
				<p>
					<span class="el-icon-arrow-right"></span>
				</p>
			</router-link>
				<div class="affirm_site">
					<div class="affirm_set">
						<van-icon class="location" name="location-o"/>
						<router-link to="/shopSite" tag="div">
							<b>{{this.affirm.name}}</b>
							<span>{{this.affirm.site}}</span>
							<p>{{this.affirm.set}}</p>
						</router-link>
					</div>
					<p>
						<span class="el-icon-arrow-right"></span>
					</p>
				</div>
			<div class="affirm_sf">
				<span>发送时间</span>
				<div>
					<p>即将送到|预计16:00送到</p>
					<p>蜂鸟专送</p>
				</div>
			</div>
			<div class="affirm_shop">
				<div>
					<p>支付方式</p>
					<p>在线支付></p>
				</div>
				<div>
					<p>红包</p>
					<p>红包支付我的账单</p>
				</div>
			</div>
			<div class="affirm_ul">
				<ul>
					<li>
						<img :src="`https://elm.cangdu.org/img/`+this.appshop.image_path" alt="">
						<p>{{this.appshop.name}}</p>
					</li>
					<li>
							<p>{{this.appshop.name}}</p>
							<p>
								<span class="affirm_price">数量:{{this.appshop.num}}</span>
								<span v-for="(val,k) in this.appshop.specfoods.slice(0,1)" :key="k">
								<span>订单￥{{val.price}}</span>
								</span>
							</p>
					</li>
					<li>
						<p>{{this.appshop.description}}</p>
						<p>总价:{{this.tallPrice}}</p>
					</li>
					<li>
						<p v-for="(item,index) in this.appshop.specfoods.slice(0,1)" :key="index">
							<span>订单￥{{item.price}}</span>
						</p>
						<p class="affirm_pay">待支付:{{this.tallPrice}}</p>
					</li>
				</ul>
			</div>
		</div>
		<footer>
			<p>待付款:￥{{this.tallPrice}}</p>
			<van-button type="primary" size="large">确认下单</van-button>
		</footer>
	</div>
</template>

<script>
	import Headnav from '../About/headNav'
	import {mapState,mapMutations} from 'vuex'
	export default {
		name: "affirmGoods",
		data(){
			return{
				flag:true,
				data:{}
			}
		},

		methods:{
			...mapMutations(['getroutAff','appGet']),
		},
		created(){
			this.affirm
			this.appGet()
			this.getroutAff()
			if(this.affirm!=''){
				this.flag=false
			}else{
				this.flag=true
			}
		},
		computed:{
			...mapState(['affirm','appshop']),
			tallPrice(){
				var Allprice = 0
				for(let i=0;i<this.appshop.specfoods.length;i++){
					Allprice = this.appshop.num*this.appshop.specfoods[i].price
				}
				return Allprice
			}
		},
		components:{
			Headnav
		}
	}
</script>

<style scoped>

</style>
